import './optimizationIcon.scss';
import messages from 'locales/messages';
import React from 'react';
import { injectIntl } from 'react-intl';
const icon = require('./Red_Hat-IT_Optimization-Gray.svg');
const OptimizationIcon = ({ className, intl }) => {
    return (React.createElement("img", { className: `optimization-icon ${className}`, src: icon, alt: intl.formatMessage(messages.optimizations), "aria-hidden": "true" }));
};
export default injectIntl(OptimizationIcon);
